@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400');
body {
  font-family: 'Roboto', sans-serif;
  background-color:#ebeced;
}

.App {
  display: flex;
  justify-content: center;
}

.calculator {
  margin-top: 130px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

  
}

.calculator > * {
  border: 1px solid rgb(206, 203, 203);
  padding: 10px;
}

.row {
  grid-column: 1/-1;
  font-size:1.5rem;
  color :black; 
  background-color: #fff;
}

#display {
  padding-top: 30px;
}
#clear {
  font-size: 1.2rem;
}
#clear:hover {
  color: #fff;
  background-color: rgb(210, 208, 208);
}


#display {
  text-align: right;
}

#clear {
  text-align: right;
}

.button {
  padding: 30px;
  display: flex;
  flex-flow: column nowrap;
  justify-content:center; 
   flex-flow: row nowrap;
   justify-content:center; 
   background: #f9f9fd; 
  }
  .button:hover {
    background: rgb(173, 173, 173); 
    color: #f9f9fd;
    cursor: pointer;
 }

 .button-operator{
  padding: 30px;
  display: flex;
  flex-flow: column nowrap;
  justify-content:center; 
   flex-flow: row nowrap;
   justify-content:center; 
  background-color:#ffff;
  font-size: 1.3rem;
  color: #00A0FF;
 } 
 .button-operator:hover {
  background-color:#00A0FF;
  color:#ffff;
  cursor: pointer;
}

#equals {
  padding: 30px;
  display: flex;
  flex-flow: column nowrap;
  justify-content:center; 
   flex-flow: row nowrap;
   justify-content:center; 
  font-size: 1.3rem;
    color: #ffff;
    background-color: #00A0FF;
 }
 #equals:hover{
  background-color: #0048ff;
  cursor: pointer;
 }
 

 .copyright {
  margin-bottom:20px;
  justify-content: center;
  text-align: center;
  margin-top: 60px;
}
   
